export default {
	name: 'xselect',
	data() {
		return {
			isActive: true
		}
	},
	methods:{
		onActiveLeft(){
			if(this.isActive == false){
				this.isActive = !this.isActive
			}
			return
		},
		onActive(){
				this.isActive = !this.isActive
		}
	}
}
